// Body
$body-bg: #f8fafc;

// Typography
$font-family-sans-serif: 'Nunito', sans-serif;
$font-size-base: 0.9rem;
$line-height-base: 1.6;

// Colors
$blue: #3490dc;
$indigo: #6574cd;
$purple: #9561e2;
$pink: #f66d9b;
$red: #e3342f;
$orange: #f6993f;
$yellow: #ffed4a;
$green: #38c172;
$teal: #4dc0b5;
$cyan: #6cb2eb;

// Custom scss
// Color classes for easy usage
.text-blue {
    color: $blue;
}

.text-indigo {
    color: $indigo;
}

.text-purple {
    color: $purple;
}

.text-pink {
    color: $pink;
}

.text-red {
    color: $red;
}

.text-orange {
    color: $orange;
}

.text-yellow {
    color: $yellow;
}

.text-green {
    color: $green;
}

.text-teal {
    color: $teal;
}

.text-cyan {
    color: $cyan;
}